'use client';

import { useState } from 'react';
import Link from 'next/link';
import MainLayout from '@/components/layout/MainLayout';

export default function AdminDashboardPage() {
  const [activeTab, setActiveTab] = useState('services');

  return (
    <MainLayout>
      <div className="bg-gray-100 min-h-screen">
        {/* Header */}
        <div className="bg-white shadow">
          <div className="max-w-7xl mx-auto py-6 px-4 sm:px-6 lg:px-8">
            <h1 className="text-3xl font-bold text-gray-900">Quản lý hệ thống</h1>
          </div>
        </div>

        <div className="max-w-7xl mx-auto py-6 sm:px-6 lg:px-8">
          {/* Tabs */}
          <div className="border-b border-gray-200">
            <nav className="-mb-px flex space-x-8" aria-label="Tabs">
              <button
                onClick={() => setActiveTab('services')}
                className={`${
                  activeTab === 'services'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                Quản lý dịch vụ
              </button>
              <button
                onClick={() => setActiveTab('blog')}
                className={`${
                  activeTab === 'blog'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                Quản lý bài viết
              </button>
              <button
                onClick={() => setActiveTab('bookings')}
                className={`${
                  activeTab === 'bookings'
                    ? 'border-blue-500 text-blue-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                } whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm`}
              >
                Đơn đặt dịch vụ
              </button>
            </nav>
          </div>

          {/* Content area */}
          <div className="mt-6">
            {activeTab === 'services' && (
              <div className="bg-white shadow overflow-hidden sm:rounded-md">
                <div className="px-4 py-5 sm:px-6 flex justify-between items-center">
                  <h2 className="text-lg font-medium text-gray-900">Danh sách dịch vụ</h2>
                  <Link
                    href="/admin/services/new"
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
                  >
                    Thêm dịch vụ mới
                  </Link>
                </div>
                <ul className="divide-y divide-gray-200">
                  {/* Service items */}
                  <li className="px-4 py-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="text-sm font-medium text-gray-900">Xét nghiệm ADN Cha - Con</h3>
                        <p className="text-sm text-gray-500">4,000,000 VNĐ</p>
                      </div>
                      <div className="flex space-x-2">
                        <button className="text-blue-600 hover:text-blue-800">Sửa</button>
                        <button className="text-red-600 hover:text-red-800">Xóa</button>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            )}

            {activeTab === 'blog' && (
              <div className="bg-white shadow overflow-hidden sm:rounded-md">
                <div className="px-4 py-5 sm:px-6 flex justify-between items-center">
                  <h2 className="text-lg font-medium text-gray-900">Danh sách bài viết</h2>
                  <Link
                    href="/admin/blog/new"
                    className="inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-white bg-blue-600 hover:bg-blue-700"
                  >
                    Thêm bài viết mới
                  </Link>
                </div>
                <ul className="divide-y divide-gray-200">
                  {/* Blog post items */}
                  <li className="px-4 py-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="text-sm font-medium text-gray-900">
                          Hiểu về xét nghiệm ADN: Tất cả những gì bạn cần biết
                        </h3>
                        <p className="text-sm text-gray-500">Đăng ngày: 15/05/2025</p>
                      </div>
                      <div className="flex space-x-2">
                        <button className="text-blue-600 hover:text-blue-800">Sửa</button>
                        <button className="text-red-600 hover:text-red-800">Xóa</button>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            )}

            {activeTab === 'bookings' && (
              <div className="bg-white shadow overflow-hidden sm:rounded-md">
                <div className="px-4 py-5 sm:px-6">
                  <h2 className="text-lg font-medium text-gray-900">Danh sách đơn đặt dịch vụ</h2>
                </div>
                <ul className="divide-y divide-gray-200">
                  {/* Booking items */}
                  <li className="px-4 py-4">
                    <div className="flex items-center justify-between">
                      <div>
                        <h3 className="text-sm font-medium text-gray-900">
                          Đơn #12345 - Xét nghiệm ADN Cha Con
                        </h3>
                        <p className="text-sm text-gray-500">Ngày đặt: 01/06/2025</p>
                        <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
                          Đang xử lý
                        </span>
                      </div>
                      <div className="flex space-x-2">
                        <button className="text-blue-600 hover:text-blue-800">Chi tiết</button>
                        <button className="text-green-600 hover:text-green-800">Cập nhật trạng thái</button>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
            )}
          </div>
        </div>
      </div>
    </MainLayout>
  );
}
